@media only screen and (max-width: 700px) {

    ul li{
        list-style: none;
    }

    .container-seccion3{
        padding-top: 0.1rem;
        margin-top: 0.3rem;
        background: rgb(131,58,180);
        background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
    }

    .container-logo-seccion3 h4{
        font-family: 'Silkscreen', cursive;
    }

    .container-logo-seccion3{
        padding-top: 0.1rem;
        display: flex;
        justify-content: space-around;
        color: #EDFFFF;
    }

    .container-seccion3-titulo h3{
        margin-top: 1rem;
        text-align: center;
        font-size: 18px;
    }

    .number-list{
        color: aqua;
        border-radius: 50%;
        border:  2px solid black;
        padding: 10px;
        background-color: black;
        
    }



    .container-list-beneficio{
        margin-top: 2rem;
    }

    .container-list-beneficio ul li{
        margin-top: 1rem;
        display: flex;
    }

    .container-list-beneficio ul li p{
        margin: auto;
        margin-left: 18px;
        font-family: 'Gemunu Libre', sans-serif;
        font-size: 24px;
    }

}